.create-store-dialog {
  min-width: 600px;
  max-width: 800px;

  @media (max-width: 768px) {
    min-width: auto;
    width: 100%;
  }

  h2[mat-dialog-title] {
    display: flex;
    align-items: center;
    gap: 12px;
    font-size: 24px;
    font-weight: 600;
    color: var(--text-primary, #222);
    margin: 0;
    padding: 24px 24px 16px;
    position: relative;

    mat-icon {
      font-size: 28px;
      width: 28px;
      height: 28px;
      color: var(--accent-primary, #667eea);
    }

    .ai-button-header {
      margin-left: auto;
      color: var(--accent-primary, #667eea);
      transition: all 0.3s ease;

      &:hover {
        background: rgba(102, 126, 234, 0.1);
        transform: scale(1.1);
      }

      &.active {
        background: rgba(102, 126, 234, 0.2);
        color: #5568d3;

        mat-icon {
          animation: pulse 1.5s ease-in-out infinite;
        }
      }
    }
  }

  mat-dialog-content {
    padding: 0 24px 24px;
    max-height: 70vh;
    overflow-y: auto;

    // 판매자 로그인 경고
    .login-warning {
      background: linear-gradient(135deg, #ff6b6b 0%, #ee5a6f 100%);
      border-radius: 12px;
      padding: 24px;
      margin-bottom: 24px;
      box-shadow: 0 4px 20px rgba(255, 107, 107, 0.3);

      .warning-content {
        display: flex;
        gap: 16px;
        color: white;

        .warning-icon {
          font-size: 48px;
          width: 48px;
          height: 48px;
          flex-shrink: 0;
          animation: shake 0.5s ease-in-out;
        }

        .warning-text {
          flex: 1;

          h4 {
            margin: 0 0 8px 0;
            font-size: 20px;
            font-weight: 600;
          }

          p {
            margin: 0 0 16px 0;
            font-size: 14px;
            opacity: 0.95;
            line-height: 1.5;
          }

          .warning-actions {
            display: flex;
            gap: 12px;

            button {
              mat-icon {
                margin-right: 8px;
              }
            }
          }
        }
      }
    }

    .store-form {
      display: flex;
      flex-direction: column;
      gap: 24px;

      .form-section {
        .section-title {
          font-size: 18px;
          font-weight: 600;
          color: var(--text-primary, #222);
          margin: 0 0 16px 0;
          padding-bottom: 8px;
          border-bottom: 2px solid var(--accent-primary, #667eea);
        }

        .full-width {
          width: 100%;
        }

        .logo-upload-section {
          label {
            display: block;
            font-size: 14px;
            font-weight: 500;
            color: var(--text-primary, #222);
            margin-bottom: 8px;
          }

          .upload-placeholder {
            border: 2px dashed var(--border-color, #e0e0e0);
            border-radius: 8px;
            padding: 32px;
            text-align: center;
            background: var(--bg-secondary, #f5f5f5);
            cursor: pointer;
            transition: all 0.3s ease;

            &:hover {
              border-color: var(--accent-primary, #667eea);
              background: var(--bg-primary, #ffffff);
            }

            mat-icon {
              font-size: 48px;
              width: 48px;
              height: 48px;
              color: var(--text-secondary, #666);
            }

            p {
              margin: 8px 0 0 0;
              color: var(--text-secondary, #666);
              font-size: 14px;
            }
          }
        }

        .address-search-group {
          display: flex;
          gap: 12px;
          align-items: flex-start;

          .zipcode-field {
            flex: 1;
          }

          .search-btn {
            margin-top: 8px;
            height: 56px;
            padding: 0 24px;
          }
        }
      }
    }
  }

  mat-dialog-actions {
    padding: 16px 24px;
    border-top: 1px solid var(--border-color, #e0e0e0);
    gap: 12px;

    button {
      min-width: 100px;

      mat-icon {
        margin-right: 4px;
        font-size: 20px;
        width: 20px;
        height: 20px;
      }

      mat-spinner {
        display: inline-block;
        margin-right: 8px;
      }
    }
  }

  // AI 프롬프트 섹션 스타일
  .ai-prompt-section {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    border-radius: 12px;
    padding: 24px;
    margin-bottom: 24px;
    box-shadow: 0 4px 20px rgba(102, 126, 234, 0.3);
    overflow: hidden;

    .ai-prompt-header {
      display: flex;
      align-items: center;
      gap: 12px;
      margin-bottom: 16px;

      .ai-icon {
        font-size: 32px;
        width: 32px;
        height: 32px;
        color: white;
        animation: pulse 2s ease-in-out infinite;
      }

      h4 {
        margin: 0;
        font-size: 18px;
        font-weight: 600;
        color: white;
      }
    }

    .ai-prompt-content {
      mat-form-field {
        width: 100%;

        ::ng-deep {
          .mat-mdc-text-field-wrapper {
            background: white;
            border-radius: 8px;
          }

          .mat-mdc-form-field-focus-overlay {
            background: rgba(255, 255, 255, 0.1);
          }
        }
      }

      .ai-prompt-actions {
        display: flex;
        gap: 12px;
        margin-top: 16px;

        button {
          mat-icon {
            margin-right: 8px;
          }

          mat-spinner {
            display: inline-block;
            margin-right: 8px;
          }
        }
      }
    }
  }

  .store-form.blurred {
    opacity: 0.5;
    pointer-events: none;
    filter: blur(2px);
    transition: all 0.3s ease;
  }

  .store-form.disabled {
    opacity: 0.4;
    pointer-events: none;
    user-select: none;
  }
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.7;
    transform: scale(1.1);
  }
}

@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-5px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(5px);
  }
}

// Material 폼 필드 스타일 커스터마이징
::ng-deep {
  .create-store-dialog {
    .mat-mdc-form-field {
      .mat-mdc-text-field-wrapper {
        .mat-mdc-form-field-flex {
          .mat-mdc-floating-label {
            color: var(--text-secondary, #666);
          }
        }
      }

      &.mat-focused {
        .mat-mdc-floating-label {
          color: var(--accent-primary, #667eea);
        }
      }
    }

    .mat-mdc-form-field-error {
      font-size: 12px;
    }

    .mat-mdc-form-field-hint {
      font-size: 12px;
      color: var(--text-secondary, #666);
    }
  }
}
